<template>
  <div>
    <div class="header">
    </div>
    <div class="wrapper">
        这里是应还款账单
    </div>
  </div>
</template>

<script type="text/ecmascript-6">
  export default {
    data () {
      return {

      }
    },
    components: {

    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .header
    position fixed
    height 44px
    top 0
    left 0
    right 0
    background $color-header-background
    display flex
    justify-content center
    align-items center
    .text
      color $color-header-text
      font-size $font-size-large
    .icon
      background url("~common/image/header/message.png") no-repeat
      position absolute
      height 14px
      width 17px
      right 16px
      top 15px
      background-size contain
      &:after
        position absolute
        background #eb4747
        width 6px
        height 6px
        border-radius 3px
        top -4px
        right -2px
        content ''
  .wrapper
    position absolute
    left 0
    right 0
    top 44px
    bottom 50px
    background $color-background
    display flex
    justify-content center

</style>



